@import url("https://fonts.googleapis.com/css2?family=Cabin+Sketch:wght@400;700&display=swap");

body, html {
	margin: 0px;
	padding: 0px;
	width: 100%;
	font-family: 'Cabin Sketch', cursive;
}

.controls {
	width: 50px;
}

.small-btn {
	width: 30px;
}

button {
	cursor: pointer;
}

.App {
	margin: auto;
	width: 80%;
}

.App h1 {
	text-align: center;
	font-size: 50px;
	font-weight: normal;
}

.App #block-length-settings {
	display: flex;
	justify-content: space-around;
}

.App #block-length-settings #break, .App #block-length-settings #session {
	width: 40%;
}

.App #block-length-settings #break label, .App #block-length-settings #session label {
	margin: 20px;
	font-size: 30px;
	display: block;
	text-align: center;
}

.App #block-length-settings #break div, .App #block-length-settings #session div {
	font-size: 40px;
	margin: auto;
	width: 100%;
	display: flex;
	justify-content: center;
}

.App #timer {
	margin: auto;
	width: 100%;
}

.App #timer h2, .App #timer p {
	text-align: center;
}

.App #timer h2 {
	font-size: 50px;
	font-weight: normal;
}

.App #timer p {
	font-size: 150px;
	font-weight: bold;
	margin: 0;
}

.App #timer #control-buttons {
	margin: auto;
	width: 100%;
	display: flex;
	justify-content: center;
}

.App #timer #control-buttons button {
	padding: 10px;
}

.App footer {
	position: fixed;
	bottom: 0;
	left: 0;
	margin: 0px;
	background-color: black;
	width: 100%;
}

.App footer #credit {
	color: white;
	text-align: center;
}

.App footer #credit a {
	text-decoration: none;
	color: white;
}

.App footer #credit a:hover {
	color: cyan;
}

button {
	border-style: none;
	background-color: transparent;
}

button:hover {
	color: red;
}

.finalCountdown {
	color: red;
}